import {
  type CardData,
  type CardType,
  type EffectInstance,
  type EffectPolarity,
  type EntityStats,
  EffectType,
} from '../types';

//   

/**  */
export type EffectTiming =
  | 'onTurnStart'    // 
  | 'onTurnEnd'      // 
  | 'onBeforeDamage'  // 
  | 'onAfterDamage'  // 
  | 'onBeforeAttack' // 
  | 'onAfterAttack'  // 
  | 'onClash'        // 
  | 'onDiceRoll'     // 
  | 'onCardPlay'     // 
  | 'passive';       // 

/**  */
export interface EffectDefinition {
  type: EffectType;
  name: string;
  polarity: EffectPolarity;
  /**  */
  timings: EffectTiming[];
  /**  */
  stackable: boolean;
  /** 0 =  */
  maxStacks: number;
  /**  */
  description: string;
}

