import { CardType, EffectType, type CardData } from '../types';

/**
 * =====================================================================
 * /
 * =====================================================================
 * 1) 
 *    -  `CardType.MAGIC`  `manaCost`
 *    - PHYSICAL / FUNCTION / DODGE `manaCost: 0`
 *
 * 2) 
 *    - `calculation = { multiplier, addition }`
 *      FinalPoint = floor(BaseDice * multiplier + addition + )
 *    - `damageLogic`
 *      relative: damage = floor(FinalPoint * scale + scaleAddition)
 *      fixed:    damage = floor(value)
 *      mixed:    damage = floor(baseValue + FinalPoint * scale + scaleAddition)
 *
 * 3) +N
 *    - +N `calculation.addition = N`
 *    - +N `damageLogic.scaleAddition = N`
 *       mixed/fixed
 *
 * 4) 
 *    - floor
 *
 * 5) 
 *    -  `hitCount` 1
 *    -  `hitCount`  UI 
 *
 * 6) 
 *    - description +N  +N
 * =====================================================================
 */

