// 
//    
// 

//   
export enum CardType {
  PHYSICAL = '',
  MAGIC = '',
  FUNCTION = '',
  DODGE = '',
  CURSE = '',
}

export type CardRarity = '' | '';

//   
/**  */
export type DamageMode = 'relative' | 'fixed' | 'mixed';

/**
 * 
 * - relative:  = FinalPoint * scale + scaleAddition
 * - fixed:     = value
 * - mixed:     = baseValue + FinalPoint * scale + scaleAddition
 */
export interface DamageLogic {
  mode: DamageMode;
  /** relative / mixed:  += FinalPoint * scale */
  scale?: number;
  /** relative / mixed:  += scaleAddition */
  scaleAddition?: number;
  /** fixed:  */
  value?: number;
  /** mixed:  */
  baseValue?: number;
}

//   
export type RerollTarget = 'self' | 'enemy' | 'none';

export interface CardTraits {
  /**  */
  combo: boolean;
  /**  */
  reroll: RerollTarget;
  /**  */
  draw: boolean;
  /**  */
  unplayable?: boolean;
  /**  */
  destroyOnClashWin?: boolean;
  /**  */
  insertCardsToEnemyDeck?: string[];
  /**  */
  purgeOnUse?: boolean;
}

//   
export interface CardCalculation {
  /**  1.0 */
  multiplier: number;
  /**  0 */
  addition: number;
}

//   

/**  */
export type EffectValueMode = 'fixed' | 'point_scale' | 'max_hp_percent';
/**  */
export type CardEffectTrigger = 'on_use' | 'on_dodge_success' | 'on_opponent_skip';

/**  */
export interface CardEffect {
  /**  on_use */
  triggers?: CardEffectTrigger[];

  /**  */
  kind:
    | 'apply_buff'     //  Buff/Debuff
    | 'heal'           // 
    | 'restore_mana'   // 
    | 'cleanse';       //  Debuff

  //  apply_buff  
  /** kind = apply_buff  */
  effectType?: EffectType;
  /** self = enemy =  */
  target?: 'self' | 'enemy';
  /**  */
  restrictedTypes?: CardType[];

  //   
  /** fixed = point_scale = FinalPoint * scalemax_hp_percent =  * scale */
  valueMode: EffectValueMode;
  /** fixed  */
  fixedValue?: number;
  /** point_scale / max_hp_percent  */
  scale?: number;

  //  cleanse  
  /**  Debuff kind = cleanse  */
  cleanseTypes?: EffectType[];
}

//   
export interface CardData {
  id: string;
  name: string;
  type: CardType;
  /**  */
  category: string;
  /**  */
  rarity: CardRarity;
  /**  Type ==   0 */
  manaCost: number;
  /**  */
  calculation: CardCalculation;
  /**  */
  damageLogic: DamageLogic;
  /** 1 */
  hitCount?: number;
  /**  */
  traits: CardTraits;
  /**  */
  cardEffects: CardEffect[];
  /**  */
  description: string;
  /**  _ */
  negativeEffect?: string;
  /**  */
  manaDrain?: number;
  /**  */
  ignoreDodge?: boolean;
  /**
   * 
   * - number
   * - object/ + /
   */
  selfDamage?: number | CardSelfDamageConfig;
  /**  */
  image?: string;
}

/**  */
export interface CardSelfDamageConfig {
  /** fixed=percent= 50  50% */
  value: number;
  /** fixed=percent= */
  mode?: 'fixed' | 'percent';
  /** hp=maxHp= */
  target?: 'hp' | 'maxHp';
}

